import React from 'react';
import Layout from '../../../components/layout'
import SEO from "../../../components/seo"
import styles from "./styles.module.css"

const splitRegex = /[\s.?]+/;

class CountWordsPage extends React.PureComponent {
  constructor() {
    super();
    this.state = {
      text: "",
      words: 0,
    }
  }

  computeWords = (text) => {
    console.log(text.split(' ').length);
    return text.split(splitRegex).filter(word => word !== "" || word !== " ").length;
  };

  timeoutFn = (text) => {
    this.setState({
        words: this.computeWords(text),
    });
  };

  textAreaChangeHandler = (e) => {
    const { value } = e.target;

    // delete previous processor
    clearTimeout(this.delayedProcessor);

    // create new
    this.delayedProcessor = setTimeout(() => this.timeoutFn(value), 2);

    this.setState({
      text: value,
    })
  };

  render() {

    return (
      <Layout withFooter={false}>
        <div>
          <SEO
            title={"Future balance calculator."}
            description={"Struggling with money? Unclear future? Let's compute your future money balance. Add your income, expenses, expected expenses and let's look what the future balance will look like."}
            keywords={["money","calculator","expenses","income","future"]}
          />
          <div>Words: {this.state.words}</div>
          <textarea className={styles.textarea} onChange={this.textAreaChangeHandler} />
        </div>
      </Layout>
    );
  }
 }

export default CountWordsPage;
